.friendMessage {
    font-family: 'Montserrat', sans-serif;
    box-sizing: border-box;
}

.friendImageAndText {
    width: 100%;
    max-width: 575px;
    display: flex;
    column-gap: 15px;
}

.friendImageAndTime{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    text-align: center;
    row-gap: 5px;
}

.friendImageAndText img {
    max-width: 48px;
}

.friendText {
    margin-bottom: 10px;
    position: relative;
    padding: 11px 13px 25px;
    border-radius: 15px;
    background-color: #F5F7FB;
    color: #000;
}

.friendText:after {
    position: absolute;
    content: '';
    display: block;
    left: 0;
    top: 100%;
    right: 0;
    bottom: 0;
    transform: translate(-20%, -100%);

    width: 0;
    height: 0;
    border: 0 solid transparent;
    border-right-width: 28px;
    border-left-width: 28px;
    border-bottom: 28px solid #F5F7FB;
}

.friendName {
    text-align: left;
    font-size: 18px;
    font-weight: 600;
}

.friendMessageText {
    font-size: 16px;
    font-weight: 300;
}

.friendTime {
    font-size: 10px;
    font-weight: 600;
}
