.greetingForm {
    width: 100%;
}

.inputAndButtonContainer {
    display: flex;
    column-gap: 15px;
}

.error {
    margin-top: 10px;
    font-size: 14px;
    font-weight: 400;
    color: #cc0000;
}

.input {
    padding: 0 20px;
    display: block;
    width: 372px;
    border: 1px solid #08080829;
    border-radius: 4px;
    min-height: 36px;
    font-size: 16px;
    font-weight: 500;
    color: #080808;
}

.input:focus {
    border: 2px solid #08080829;
}

.errorInput {
    display: block;
    width: 372px;
    border: 2px solid red;
    border-radius: 4px;
    min-height: 36px;
    font-size: 16px;
    font-weight: 500;
    color: #080808;
}

.button {
    height: 36px;
    padding: 8px 24px;
    cursor: pointer;
    border-radius: 4px;
    background-color: #0066cc;
    color: #ffffff;
}

.button:focus {
}

.button:active {
}

.button:disabled {
    opacity: 50%;
}

.text {
    margin-bottom: 10px;
    font-size: 14px;
    font-weight: 400;
    color: #080808;
    opacity: 50%;
}

.greeting {
    font-size: 14px;
    font-weight: 500;
    color: #080808;
}
