.message{
    font-family: 'Montserrat', sans-serif;
    box-sizing: border-box;
}

.imageAndText {
    width: 100%;
    max-width: 575px;
    display: flex;
    flex-direction: row-reverse;
    column-gap: 15px;
    margin-left: auto;
}

.imageAndTime{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    text-align: center;
    row-gap: 5px;
}

.imageAndText img {
    max-width: 48px;
}

.text {
    margin-bottom: 10px;
    position: relative;
    padding: 11px 13px 25px;
    border-radius: 15px;
    background-color: #0066CC;
    color: #fff;
}

.text:after {
    position: absolute;
    content: '';
    display: block;
    left: 100%;
    top: 100%;
    right: 0;
    bottom: 0;
    transform: translate(-80%, -100%);

    width: 0;
    height: 0;
    border: 0 solid transparent;
    border-right-width: 28px;
    border-left-width: 28px;
    border-bottom: 28px solid #0066CC;
}

.name {
    text-align: right;
    font-size: 18px;
    font-weight: 600;

}

.messageText {
    font-size: 16px;
    font-weight: 300;

}

.time {
    font-size: 10px;
    font-weight: 600;
}
