.sendForm {
    font-family: 'Montserrat', sans-serif;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.textarea {
    padding: 18px 300px 18px 24px;
    width: 100%;
    max-width: 976px;
    min-height: 40px;
    box-sizing: border-box;

    border-radius: 50px;
    border: none;
    background-color: #F5F7FB;

    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    font-weight: 300;

    &::placeholder{
        max-width: 760px;
        min-height: max-content;
    }
}

.button {
    width: 100%;
    max-width: 120px;
    min-height: 40px;
    padding: 5px 20px;

    border-radius: 50px;
    border: none;
    background-color: #0066CC;

    vertical-align: middle;
    font-size: 16px;
    font-weight: 700;
    color: #fff;

    cursor: pointer;
}

.button:hover {
    background-color: #808080;
    transition: all 0.2s linear;
}
.button:active {
    background-color: #696969;
}
